.matchContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top: solid 1px #000;
    border-bottom: solid 1px #000;
}

.match{
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.matchNum{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: solid 2px #000;
}

.matchNum p{
    margin: 10px;
}

.matchTeam{
    margin: 5px;
}

.puntosCont{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.puntosCont p{
    margin: 5px;
}

.puntaje{
    background-color: aquamarine;
    padding: 5px;
    height: 15px;
    width: 15px;
    border-radius: 100%;
    font-size: 15px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 1px #000;
}